___CSS_LOADER_EXPORT___.push([module.id, `* {
    font-family: 'Segoe UI', sans-serif;
}

body {
    display: grid;
    place-items: center;
    margin: 0;
    background-image: url('https://i.imgur.com/CUVyedQ.jpg');
    background-repeat: no-repeat;
    background-size: cover;
    position: relative;
    height: 100vh;

}

body::before {
    content: "";
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.6);
    z-index: -1;
    position: absolute;
}

.conteudo {
    display: flex;
    flex-direction: column;
    gap: 10px;
    padding-top: 50px;
}


.descricao {
    font-size: 22px;
    font-weight: 600;
    color: #f9f9f9;
}


#card {
    width: 240px;
    height: 360px;
    border: 2px solid #000;
    border-radius: 10px;
    background-color: #fff;
    display: flex;
    flex-direction: column;
    overflow: hidden;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.305);
    background-color: black;

}

#card-content {
    background-color: #fff;
    margin: 10px;
    flex-grow: 1;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

#foto {
    width: 80%;
    height: 50%;
    margin: 10px;

    background-size: cover;
}

#info {
    padding: 10px;
    flex-grow: 1;
    text-align: center;
    background-color: #f0f0f0;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    border-top: 3px solid black;
    size: fill;

}

#info h2 {
    margin: 0;
    font-size: 18px;
    font-weight: bold;
    padding-bottom: 30px;
}

#info h1 {
    margin: 0;
    font-size: 12px;
    font-weight: bold;
    padding-bottom: 30px;
}

#info p {
    margin: 0;
    font-size: 16px;

}

.botao {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 20px;

}

.btnVoltar {

    width: 60px;
    height: 25px;
    font-weight: bold;

}`, "",{"version":3,"sources":["webpack://./src/views/pokemon-detalhes/pokemon-detalhes.css"],"names":[],"mappings":"AAAA;IACI,mCAAmC;AACvC;;AAEA;IACI,aAAa;IACb,mBAAmB;IACnB,SAAS;IACT,wDAAwD;IACxD,4BAA4B;IAC5B,sBAAsB;IACtB,kBAAkB;IAClB,aAAa;;AAEjB;;AAEA;IACI,WAAW;IACX,MAAM;IACN,OAAO;IACP,WAAW;IACX,YAAY;IACZ,oCAAoC;IACpC,WAAW;IACX,kBAAkB;AACtB;;AAEA;IACI,aAAa;IACb,sBAAsB;IACtB,SAAS;IACT,iBAAiB;AACrB;;;AAGA;IACI,eAAe;IACf,gBAAgB;IAChB,cAAc;AAClB;;;AAGA;IACI,YAAY;IACZ,aAAa;IACb,sBAAsB;IACtB,mBAAmB;IACnB,sBAAsB;IACtB,aAAa;IACb,sBAAsB;IACtB,gBAAgB;IAChB,0CAA0C;IAC1C,uBAAuB;;AAE3B;;AAEA;IACI,sBAAsB;IACtB,YAAY;IACZ,YAAY;IACZ,aAAa;IACb,sBAAsB;IACtB,mBAAmB;IACnB,uBAAuB;AAC3B;;AAEA;IACI,UAAU;IACV,WAAW;IACX,YAAY;;IAEZ,sBAAsB;AAC1B;;AAEA;IACI,aAAa;IACb,YAAY;IACZ,kBAAkB;IAClB,yBAAyB;IACzB,aAAa;IACb,sBAAsB;IACtB,8BAA8B;IAC9B,2BAA2B;IAC3B,UAAU;;AAEd;;AAEA;IACI,SAAS;IACT,eAAe;IACf,iBAAiB;IACjB,oBAAoB;AACxB;;AAEA;IACI,SAAS;IACT,eAAe;IACf,iBAAiB;IACjB,oBAAoB;AACxB;;AAEA;IACI,SAAS;IACT,eAAe;;AAEnB;;AAEA;IACI,aAAa;IACb,uBAAuB;IACvB,mBAAmB;IACnB,aAAa;;AAEjB;;AAEA;;IAEI,WAAW;IACX,YAAY;IACZ,iBAAiB;;AAErB","sourcesContent":["* {\r\n    font-family: 'Segoe UI', sans-serif;\r\n}\r\n\r\nbody {\r\n    display: grid;\r\n    place-items: center;\r\n    margin: 0;\r\n    background-image: url('https://i.imgur.com/CUVyedQ.jpg');\r\n    background-repeat: no-repeat;\r\n    background-size: cover;\r\n    position: relative;\r\n    height: 100vh;\r\n\r\n}\r\n\r\nbody::before {\r\n    content: \"\";\r\n    top: 0;\r\n    left: 0;\r\n    width: 100%;\r\n    height: 100%;\r\n    background-color: rgba(0, 0, 0, 0.6);\r\n    z-index: -1;\r\n    position: absolute;\r\n}\r\n\r\n.conteudo {\r\n    display: flex;\r\n    flex-direction: column;\r\n    gap: 10px;\r\n    padding-top: 50px;\r\n}\r\n\r\n\r\n.descricao {\r\n    font-size: 22px;\r\n    font-weight: 600;\r\n    color: #f9f9f9;\r\n}\r\n\r\n\r\n#card {\r\n    width: 240px;\r\n    height: 360px;\r\n    border: 2px solid #000;\r\n    border-radius: 10px;