var PokemonService = /** @class */ (function () {
    function PokemonService() {
    }
    PokemonService.prototype.pesquisarPokemon = function (nome) {
        var _this = this;
        var url = 'https://pokeapi.co/api/v2/pokemon/' + nome;
        return fetch(url)
            .then(function (res) { return res.json(); })
            .then(function (obj) { return _this.mapearPokemon(obj); });
    };
    PokemonService.prototype.pesquisarPokemonCard = function (nome) {
        var _this = this;
        var url = 'https://pokeapi.co/api/v2/pokemon/' + nome;
        return fetch(url)
            .then(function (res) { return res.json(); })
            .then(function (obj) { return _this.mapearPokemonCard(obj); });
    };
    PokemonService.prototype.pesquisarListaPokemons = function () {
        var _this = this;
        var url = 'https://pokeapi.co/api/v2/pokemon/?offset=0&limit=151';
        return fetch(url)
            .then(function (res) { return res.json(); })
            .then(function (obj) { return _this.mapearListaPokemons(obj.results); });
    };
    PokemonService.prototype.mapearListaPokemons = function (objetos) {
        var _this = this;
        var pokemons = objetos.map(function (obj) { return _this.pesquisarPokemon(obj.name); });
        return Promise.all(pokemons);
    };
    PokemonService.prototype.mapearPokemonCard = function (obj) {
        console.log(obj);
        return {
            id: obj.id,
            nome: obj.name,
            tipo: obj.types[0].type.name,
            spriteUrl: obj['sprites']['other']['official-artwork']['front_default'],
        };
    };
    PokemonService.prototype.mapearPokemon = function (obj) {
        if (obj.id < 650) {
            return {
                id: obj.id,
                nome: obj.name,
                tipo: obj.type,
                spriteUrl: obj['sprites']['versions']['generation-v']['black-white']['animated']['front_default'],
            };
        }
        else {
            return {
                id: obj.id,
                nome: obj.name,
                tipo: obj.type,
                spriteUrl: obj.sprites.other.home.front_default,
            };
        }
    };
    return PokemonService;
}());
export { PokemonService };
