import { PokemonService } from "../../services/pokemon.service";
import './pokemon-detalhes.css';
var PokemonDetalhes = /** @class */ (function () {
    function PokemonDetalhes() {
        this.pokemonService = new PokemonService();
        this.registrarElementos();
        this.registrarEventos();
        var url = new URLSearchParams(window.location.search);
        var nome = url.get('nome');
        this.pesquisarPokemonPorNome(nome);
    }
    PokemonDetalhes.prototype.registrarElementos = function () {
        this.formPrincipal = document.getElementById("formPrincipal");
        this.txtPesquisa = document.getElementById("txtPesquisa");
        this.btnLimpar = document.getElementById("btnLimpar");
        this.pnlConteudo = document.getElementById("pnlConteudo");
        this.card = document.getElementById("card");
        this.cardContent = document.getElementById("card-content");
        this.foto = document.getElementById("foto");
        this.info = document.getElementById("info");
        this.nome = document.getElementById("nome");
        this.tipo = document.getElementById("tipo");
        this.numero = document.getElementById("numero");
        this.btnVoltar = document.getElementById("btnVoltar");
    };
    PokemonDetalhes.prototype.registrarEventos = function () {
        //     this.formPrincipal
        //         .addEventListener('submit', (sender) => this.buscar(sender));
        var _this = this;
        this.btnVoltar
            .addEventListener('click', function () { return _this.redirecionarUsuario(); });
    };
    PokemonDetalhes.prototype.redirecionarUsuario = function () {
        window.location.href = "index.html";
    };
    PokemonDetalhes.prototype.buscar = function (sender) {
        sender.preventDefault();
        if (!this.txtPesquisa.value)
            return;
        var nome = this.txtPesquisa.value;
        this.txtPesquisa.value = '';
        this.limparCard();
        this.pesquisarPokemonPorNome(nome);
    };
    PokemonDetalhes.prototype.limparCard = function () {
        var _a;
        (_a = this.pnlConteudo.querySelector('.card-pokemon')) === null || _a === void 0 ? void 0 : _a.remove();
    };
    PokemonDetalhes.prototype.pesquisarPokemonPorNome = function (nome) {
        var _this = this;
        this.pokemonService.pesquisarPokemonCard(nome)
            .then(function (poke) { return _this.atualizarCard(poke); });
    };
    PokemonDetalhes.prototype.atualizarCard = function (pokemon) {
        this.foto.src = pokemon.spriteUrl;
        var nome = this.PrimeiraLetraMaiscula(pokemon.nome);
        this.nome.textContent = nome;
        var numeroFormatado = this.formatNumberWithZeros(pokemon.id);
        this.numero.textContent = numeroFormatado;
        var tipo = this.PrimeiraLetraMaiscula(pokemon.tipo);
        this.tipo.textContent = tipo;
        var cor = this.verificarTipo(tipo);
        this.colorirCard(cor);
    };
    PokemonDetalhes.prototype.colorirCard = function (cor) {
        this.card.style.backgroundColor = cor;
        this.tipo.style.backgroundColor = cor;
    };
    PokemonDetalhes.prototype.verificarTipo = function (tipo) {
        switch (tipo.toLowerCase()) {
            case "normal":
                return '#A8A878'; // Normal
            case "fire":
                return '#F08030'; // Fogo
            case "water":
                return '#6890F0'; // Água
            case "electric":
                return '#F8D030'; // Elétrico
            case "grass":
                return '#78C850'; // Grama
            case "ice":
                return '#98D8D8'; // Gelo
            case "fighting":
                return '#C03028'; // Lutador
            case "poison":
                return '#A040A0'; // Veneno
            case "ground":
                return '#E0C068'; // Terrestre
            case "flying":
                return '#A890F0'; // Voador
            case "psychic":
                return '#F85888'; // Psíquico
            case "bug":
                return '#A8B820'; // Inseto
            case "rock":
                return '#B8A038'; // Pedra
            case "ghost":
                return '#705898'; // Fantasma
            case "dark":
                return '#705848'; // Sombrio
            case "steel":
                return '#B8B8D0'; // Aço
            case "fairy":
                return '#EE99AC'; // Fada
            default:
                return 'gray';
        }
    };
    PokemonDetalhes.prototype.PrimeiraLetraMaiscula = function (text) {
        return text.charAt(0).toUpperCase() + text.slice(1);
    };
    PokemonDetalhes.prototype.formatNumberWithZeros = function (number) {
        var formattedNumber = "#".concat(String(number).padStart(4, '0'));
        return formattedNumber;
    };
    return PokemonDetalhes;
}());
window.addEventListener('load', function () { return new PokemonDetalhes(); });
